import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { MapaNewDTO } from 'src/app/models/mapa/mapa-new.dto';
import { MapaNewUpdateDTO } from 'src/app/models/mapa/mapa-new-update.dto';

@Injectable({
  providedIn: 'root'
})
export class MapaService {

  private staticID: string;

  public getStaticID(): string {
    return this.staticID;
  }

  public setStaticID(id: string): void {
    this.staticID = id;
  }

  constructor(private http: HttpClient, private storage: StorageService) { }

  findPageByCriador(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}mapa/criador`,
      {
        params,
        observe: 'body',
      });
  }

  insert(dto: MapaNewDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}mapa`,
      dto);
  }

  update(dto: MapaNewUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}mapa`,
      dto);
  }

  findById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}mapa/${id}`,
      {
        responseType: 'json',
        observe: 'body',
      });
  }
}
