import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { FaccaoNewDTO } from 'src/app/models/faccao/faccao-new.dto';
import { FaccaoNewUpdateDTO } from 'src/app/models/faccao/faccao-update-new.dto';

@Injectable({
  providedIn: 'root'
})
export class FaccoesService {

  private staticID: string;

  public getStaticID(): string {
    return this.staticID;
  }

  public setStaticID(id: string): void {
    this.staticID = id;
  }

  constructor(private http: HttpClient, private storage: StorageService) { }

  findPageByCriador(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}faccao/criador`,
      {
        params,
        observe: 'body',
      });
  }

  insert(dto: FaccaoNewDTO): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}faccao`,
      dto);
  }

  update(dto: FaccaoNewUpdateDTO): Observable<any> {
    return this.http.put(`${API_CONFIG.baseUrl}faccao`,
      dto);
  }

  findById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}faccao/${id}`);
  }
}
