import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AlertController, ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';

import { MissaoNewDTO } from 'src/app/models/historia/missao-new.dto';
import { TipoMissao } from 'src/app/config/rpg.config';
import { StorageService } from 'src/app/services/storage-service.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MissaoService } from 'src/app/services/historia/missao.service';
import { MissaoUpdateDTO } from 'src/app/models/historia/missao-update.dto';

@Component({
  selector: 'app-missao-update',
  templateUrl: './missao-update.component.html',
  styleUrls: ['./missao-update.component.scss'],
})
export class MissaoUpdateComponent implements OnInit {

  formMissao: FormGroup;
  newMissao: MissaoUpdateDTO;

  recompensas: string[] = [];
  recompensa: string = "";

  @Input('menu')
  menu: boolean = true;

  @Input('cancelarBTN')
  cancelarBTN: boolean = false;
  achado: boolean;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';

  novo: boolean = false;

  tipo = TipoMissao;

  page;
  paginaAtual = 0;

  requestUpdate = false;

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private missaoService: MissaoService,
    private paginaService: PaginacaoService, private toast: MessageService,
    private modalCtrl: ModalController) {
    this.formMissao = this.formBuilder.group({
      nome: ['', [Validators.required]],
      localizacao: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      tipoMissao: ['', [Validators.required]],
      imgURL: [''],
      isPrivate: ['', [Validators.required]],

    });
  }

  ngOnInit() {
    this.requestUpdate = false;
    this.loading = true;


    const id = this.missaoService.getStaticID();
    this.missaoService.findById(id)
      .subscribe(res => {

        const { descricao, nome, localizacao, imgURL, tipoMissao, isPrivate, recompensas } = res;

        this.formMissao.get('nome').setValue(nome);
        this.formMissao.get('descricao').setValue(descricao);
        this.formMissao.get('localizacao').setValue(localizacao);
        this.formMissao.get('imgURL').setValue(imgURL);
        this.formMissao.get('tipoMissao').setValue(this.getCurrentType(tipoMissao));
        if (isPrivate == false) {
          this.formMissao.get('isPrivate').setValue(false);
        } else {
          this.formMissao.get('isPrivate').setValue(true);
        }

        this.recompensas = recompensas;

        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }

  salvar() {
    this.loading = true;
    this.newMissao = {
      idMissao: this.missaoService.getStaticID(),
      nome: this.formMissao.get('nome').value,
      descricao: this.formMissao.get('descricao').value,
      localizacao: this.formMissao.get('localizacao').value,
      recompensas: this.recompensas,
      tipoMissao: this.formMissao.get('tipoMissao').value,
      imgURL: this.formMissao.get('imgURL').value,
      isPrivate: this.formMissao.get('isPrivate').value
    };

    this.missaoService.update(this.newMissao)
      .subscribe(res => {
        this.loading = false;
        this.requestUpdate = true;
        this.callToast('success', 'Atualizado com sucesso!', 'Missão atualizada');
      },
        error => {
          this.loading = false;
        });

    //this.loading = false;
  }

  inserirRecompensa() {
    if (this.recompensa.length < 5) {
      this.presentError('recompensa');
    } else {
      this.recompensas.push(this.recompensa);
      this.recompensa = "";
    }
  }

  async presentError(texto: string) {
    const alert = await this.alertController.create({
      header: 'Valor inválido',
      message: 'Insira um ' + texto + ' com pelo menos 5 caracteres',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  novaMissao() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.missaoService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  getCurrentType(tipo) {

    switch (tipo) {
      case "PRINCIPAL":
        return 0;
        break;

      case "SECUNDARIA":
        return 1;
        break;


      case "ATIVIDADE":
        return 2;
        break;

      case "EVENTO":
        return 3;
        break;
    }
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

}
