import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { StorageService } from 'src/app/services/storage-service.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MessageService } from 'primeng/api';
import { AlertController, ModalController } from '@ionic/angular';
import { FaccoesService } from 'src/app/services/criacoes/faccoes.service';
import { FaccaoNewUpdateDTO } from 'src/app/models/faccao/faccao-update-new.dto';

@Component({
  selector: 'app-faccoes-update',
  templateUrl: './faccoes-update.component.html',
  styleUrls: ['./faccoes-update.component.scss'],
})
export class FaccoesUpdateComponent implements OnInit {

  @Input('menu')
  menu: boolean = true;
  @Input('cancelarBTN')
  cancelarBTN: boolean = false;

  @Input('add')
  add: boolean = false;

  formFaccao: FormGroup;
  newFaccao: FaccaoNewUpdateDTO;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';
  novo: boolean = false;

  paginaAtual = 0;
  paginaAtualMissao = 0;

  requestUpdate: boolean = false;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private faccaoService: FaccoesService,
    private paginaService: PaginacaoService, private toast: MessageService,
    private modalCtrl: ModalController) {
    this.formFaccao = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      objetivo: ['', [Validators.required]],
      imgURL: [''],
      isPrivate: ['', [Validators.required]],
    });
  }

  ngOnInit() {
    this.requestUpdate = false;
    this.loading = true;


    const id = this.faccaoService.getStaticID();
    this.faccaoService.findById(id)
      .subscribe(res => {

        const { descricao, nome, objetivo, imgURL, isPrivate } = res;

        this.formFaccao.get('nome').setValue(nome);
        this.formFaccao.get('descricao').setValue(descricao);
        this.formFaccao.get('objetivo').setValue(objetivo);
        this.formFaccao.get('imgURL').setValue(imgURL);
        if (isPrivate == false) {
          this.formFaccao.get('isPrivate').setValue(false);
        } else {
          this.formFaccao.get('isPrivate').setValue(true);
        }

        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }

  getSelectedObj(evt) {
    this.obj.emit(evt);
  }

  salvar() {
    this.loading = true;
    this.newFaccao = {
      id: this.faccaoService.getStaticID(),
      nome: this.formFaccao.get('nome').value,
      descricao: this.formFaccao.get('descricao').value,
      objetivo: this.formFaccao.get('objetivo').value,
      imgURL: this.formFaccao.get('imgURL').value,
      isPrivate: this.formFaccao.get('isPrivate').value
    };

    this.faccaoService.update(this.newFaccao)
      .subscribe(res => {
        this.loading = false;
        this.requestUpdate = true;
        this.callToast('success', 'Atualizado com sucesso!', 'Facção atualizada');
      },
        error => {
          this.loading = false;
        });

  }

  novaFaccao() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.faccaoService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

}
