import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { StorageService } from 'src/app/services/storage-service.service';
import { AlertController, ModalController } from '@ionic/angular';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { CapituloNewDTO } from 'src/app/models/historia/capitulo-new.dto';
import { CapituloService } from 'src/app/services/historia/capitulo.service';
import { MissaoService } from 'src/app/services/historia/missao.service';
import { MessageService } from 'primeng/api';
import { CapituloUpdateDTO } from 'src/app/models/capitulo/capitulo-update.dto';
import { CapituloNewUpdateDTO } from 'src/app/models/historia/capitulo-new-update.dto';

@Component({
  selector: 'app-capitulo-update',
  templateUrl: './capitulo-update.component.html',
  styleUrls: ['./capitulo-update.component.scss'],
})
export class CapituloUpdateComponent implements OnInit {

  formCapitulo: FormGroup;
  newCapitulo: CapituloNewUpdateDTO;

  idMissoes: string[] = [];
  idMissao: string = "";

  @Input('menu')
  menu: boolean = true;

  @Input('cancelarBTN')
  cancelarBTN: boolean = false;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';
  novo: boolean = false;

  paginaAtual = 0;
  paginaAtualMissao = 0;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  @Input('add')
  add: boolean = false;

  requestUpdate: boolean = false;

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private capituloService: CapituloService,
    private missaoService: MissaoService, private paginaService: PaginacaoService,
    private modalCtrl: ModalController, private toast: MessageService) {
    this.formCapitulo = this.formBuilder.group({
      numCapitulo: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      titulo: ['', [Validators.required]],
      historia: ['', [Validators.required]],
      resumoCapPassado: ['', [Validators.required]],
      imgURL: [''],
      isPrivate: ['', [Validators.required]],
    });
  }

  ngOnInit() {

    this.consultarMissao();

    this.requestUpdate = false;
    this.loading = true;


    const id = this.capituloService.getStaticID();
    this.capituloService.findById(id)
      .subscribe(res => {
        const { numCapitulo, titulo, historia, imgURL, resumoCapPassado, isPrivate, missoes } = res;

        this.formCapitulo.get('numCapitulo').setValue(numCapitulo);
        this.formCapitulo.get('titulo').setValue(titulo);
        this.formCapitulo.get('historia').setValue(historia);
        this.formCapitulo.get('resumoCapPassado').setValue(resumoCapPassado);
        this.formCapitulo.get('historia').setValue(historia);
        this.formCapitulo.get('imgURL').setValue(imgURL);
        if (isPrivate == false) {
          this.formCapitulo.get('isPrivate').setValue(false);
        } else {
          this.formCapitulo.get('isPrivate').setValue(true);
        }

        this.idMissoes = missoes.map(m => m.idMissao);

        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }

  getSelectedChapt(evt) {
    this.obj.emit(evt);
  }

  salvar() {
    this.loading = true;
    this.newCapitulo = {
      numCapitulo: this.formCapitulo.get('numCapitulo').value,
      titulo: this.formCapitulo.get('titulo').value,
      historia: this.formCapitulo.get('historia').value,
      idMissoes: this.idMissoes,
      resumoCapPassado: this.formCapitulo.get('resumoCapPassado').value,
      imgURL: this.formCapitulo.get('imgURL').value,
      isPrivate: this.formCapitulo.get('isPrivate').value,
      idCapitulo: this.capituloService.getStaticID()
    };

    this.capituloService.update(this.newCapitulo)
      .subscribe(res => {
        this.loading = false;
        this.requestUpdate = true;
        this.callToast('success', 'Atualizado com sucesso!', 'Missão atualizada');
      },
        error => {
          this.loading = false;
        });

  }

  inserirManualmente() {
    this.missaoService.findById(this.idMissao)
      .subscribe(res => {
        this.pushIdMissao(res.idMissao);
      },
        error => error);

  }

  async presentError(texto: string) {
    const alert = await this.alertController.create({
      header: 'Valor inválido',
      message: 'Insira um ' + texto + ' com pelo menos 5 caracteres',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  novoCapitulo() {
    this.consultarMissao();
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  getNotificationMissao(evt) {
    if (this.paginaAtualMissao != evt) {
      this.paginaAtualMissao = evt;
      this.consultarMissao();
    }
  }

  getSelectedObj(evt) {
    this.pushIdMissao(evt.id);
  }

  pushIdMissao(id) {
    if (this.idMissoes.length == 0 || !this.idMissoes.includes(id)) {
      this.idMissoes.push(id);
      this.callAdd();
    } else {
      this.callFail(id);
    }
  }

  consultar() {
    const p = this.capituloService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  consultarMissao() {
    const p = this.missaoService.findPageByCriador(this.paginaAtualMissao.toString());
    this.paginaService.setPromisse(p);
    //this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  async presentExistente(id: string) {
    const alert = await this.alertController.create({
      header: 'Já incluso',
      message: 'A missao: ' + id + ' já está inclusa!',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  callAdd() {
    this.toast.clear();
    this.toast.add({
      severity: 'success',
      summary: 'Adicionado',
      detail: 'Adicionado com sucesso!'
    });
  }

  callFail(id) {
    this.toast.clear();
    this.toast.add({
      severity: 'error',
      summary: 'Já incluso',
      detail: 'A missao: ' + id + ' já está inclusa!'
    });
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

}
