import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MissaoNewDTO } from 'src/app/models/historia/missao-new.dto';
import { TipoMissao } from 'src/app/config/rpg.config';
import { StorageService } from 'src/app/services/storage-service.service';
import { AlertController, ModalController } from '@ionic/angular';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MissaoService } from 'src/app/services/historia/missao.service';
import { MissaoUpdateComponent } from '../missao-update/missao-update.component';

@Component({
  selector: 'app-missao',
  templateUrl: './missao.component.html',
  styleUrls: ['./missao.component.scss'],
})
export class MissaoComponent implements OnInit {

  formMissao: FormGroup;
  newMissao: MissaoNewDTO;

  recompensas: string[] = [];
  recompensa: string = "";

  @Input('menu')
  menu: boolean = true;

  @Input('cancelarBTN')
  cancelarBTN: boolean = false;
  achado: boolean;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';

  novo: boolean = false;

  tipo = TipoMissao;

  page;
  paginaAtual = 0;

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private missaoService: MissaoService,
    private paginaService: PaginacaoService, public modalCtrl: ModalController,) {
    this.formMissao = this.formBuilder.group({
      nome: ['', [Validators.required]],
      localizacao: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      tipoMissao: ['', [Validators.required]],
      imgURL: ['']
    });
  }

  ngOnInit() { }

  salvar() {
    this.loading = true;
    this.newMissao = {
      nome: this.formMissao.get('nome').value,
      descricao: this.formMissao.get('descricao').value,
      localizacao: this.formMissao.get('localizacao').value,
      recompensas: this.recompensas,
      tipoMissao: this.formMissao.get('tipoMissao').value,
      idCriador: this.storage.getLocalUserId(),
      imgURL: this.formMissao.get('imgURL').value,
    };

    this.missaoService.insert(this.newMissao)
      .subscribe(res => {
        this.loading = false;
        this.presentAdicionar(res.idMissao);
        this.formMissao.get('nome').setValue('');
        this.formMissao.get('descricao').setValue('');
        this.formMissao.get('localizacao').setValue('');
        this.formMissao.get('tipoMissao').setValue('');
        this.recompensas = [];
        this.formMissao.get('imgURL').setValue('');
      },
        error => {
          this.loading = false;
        });

  }

  inserirRecompensa() {
    if (this.recompensa.length < 5) {
      this.presentError('recompensa');
    } else {
      this.recompensas.push(this.recompensa);
      this.recompensa = "";
    }
  }

  async presentError(texto: string) {
    const alert = await this.alertController.create({
      header: 'Valor inválido',
      message: 'Insira um ' + texto + ' com pelo menos 5 caracteres',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  novaMissao() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.missaoService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  async selectedObj(event) {
    this.missaoService.setStaticID(event.id)
    const modal = await this.modalCtrl.create({
      component: MissaoUpdateComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'princi'
    });

    modal.onDidDismiss()
      .then(res => {
        const { data } = res;
        //console.log(res);
        if (data == true) {
          const p = this.missaoService.findPageByCriador(this.paginaAtual.toString());
          this.paginaService.setPromisse(p);
          this.paginaService.requestRefresh(true);
        }
      });

    return await modal.present();
  }

}
