import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FaccaoNewDTO } from 'src/app/models/faccao/faccao-new.dto';
import { StorageService } from 'src/app/services/storage-service.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MessageService } from 'primeng/api';
import { AlertController, ModalController } from '@ionic/angular';
import { FaccoesService } from 'src/app/services/criacoes/faccoes.service';
import { FaccoesUpdateComponent } from '../faccoes-update/faccoes-update.component';

@Component({
  selector: 'app-faccoes',
  templateUrl: './faccoes.component.html',
  styleUrls: ['./faccoes.component.scss'],
})
export class FaccoesComponent implements OnInit {

  @Input('menu')
  menu: boolean = true;
  @Input('cancelarBTN')
  cancelarBTN: boolean = false;

  @Input('add')
  add: boolean = false;

  formFaccao: FormGroup;
  newFaccao: FaccaoNewDTO;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';
  novo: boolean = false;

  paginaAtual = 0;
  paginaAtualMissao = 0;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private faccaoService: FaccoesService,
    private paginaService: PaginacaoService, private toast: MessageService,
    private modalCtrl: ModalController) {
    this.formFaccao = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      objetivo: ['', [Validators.required]],
      imgURL: ['']
    });
  }

  ngOnInit() { }

  getSelectedObj(evt) {
    this.obj.emit(evt);
  }

  salvar() {
    this.loading = true;
    this.newFaccao = {
      nome: this.formFaccao.get('nome').value,
      descricao: this.formFaccao.get('descricao').value,
      objetivo: this.formFaccao.get('objetivo').value,
      idCriador: this.storage.getLocalUserId(),
      imgURL: this.formFaccao.get('imgURL').value,
    };

    this.faccaoService.insert(this.newFaccao)
      .subscribe(res => {
        this.loading = false;
        this.presentAdicionar(res.idFaccao);
        this.formFaccao.get('nome').setValue('');
        this.formFaccao.get('descricao').setValue('');
        this.formFaccao.get('objetivo').setValue('');
        this.formFaccao.get('imgURL').setValue('');
        this.copyToClipboard(res.idFaccao);
      },
        error => {
          this.loading = false;
        });

  }

  novaFaccao() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.faccaoService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  async selectedObj(event) {
    this.faccaoService.setStaticID(event.id)
    const modal = await this.modalCtrl.create({
      component: FaccoesUpdateComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'princi'
    });

    modal.onDidDismiss()
      .then(res => {
        const { data } = res;
        //console.log(res);
        if (data == true) {
          const p = this.faccaoService.findPageByCriador(this.paginaAtual.toString());
          this.paginaService.setPromisse(p);
          this.paginaService.requestRefresh(true);
        }
      });

    return await modal.present();
  }

}
