import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { MessageService } from 'primeng/api';

import { StorageService } from 'src/app/services/storage-service.service';
import { AlertController, ModalController } from '@ionic/angular';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MapaService } from 'src/app/services/mapa/mapa.service';
import { MapaNewUpdateDTO } from 'src/app/models/mapa/mapa-new-update.dto';

@Component({
  selector: 'app-mapa-update',
  templateUrl: './mapa-update.component.html',
  styleUrls: ['./mapa-update.component.scss'],
})
export class MapaUpdateComponent implements OnInit {

  @Input('menu')
  menu: boolean = true;
  @Input('cancelarBTN')
  cancelarBTN: boolean = false;

  @Input('add')
  add: boolean = false;

  formMapa: FormGroup;
  newMapa: MapaNewUpdateDTO;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';
  novo: boolean = false;

  paginaAtual = 0;
  paginaAtualMissao = 0;

  requestUpdate: boolean = false;

  @Output('obj')
  obj: EventEmitter<any> = new EventEmitter();

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private mapaService: MapaService,
    private paginaService: PaginacaoService, private toast: MessageService,
    public modalCtrl: ModalController) {
    this.formMapa = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      urlMapa: ['', [Validators.required]],
      isPrivate: ['', [Validators.required]],
    });
  }

  ngOnInit() {
    this.requestUpdate = false;
    this.loading = true;


    const id = this.mapaService.getStaticID();
    this.mapaService.findById(id)
      .subscribe(res => {

       // console.log(res);

        const { idMapa, descricao, nome, urlMapa, isPrivate } = res;

        this.formMapa.get('nome').setValue(nome);
        this.formMapa.get('descricao').setValue(descricao);
        this.formMapa.get('urlMapa').setValue(urlMapa);
        if (isPrivate == false) {
          this.formMapa.get('isPrivate').setValue(false);
        } else {
          this.formMapa.get('isPrivate').setValue(true);
        }

        this.loading = false;
      },
        error => {
          console.log(error)
          this.loading = false;
        });
  }


  salvar() { //Implementar a atualização e deixar bonito
    const id = this.mapaService.getStaticID();
    this.requestUpdate = true;
    this.loading = true;
    this.newMapa = {
      id,
      nome: this.formMapa.get('nome').value,
      descricao: this.formMapa.get('descricao').value,
      urlMapa: this.formMapa.get('urlMapa').value,
      isPrivate: this.formMapa.get('isPrivate').value
    };

    //console.log(this.newMapa);

    this.mapaService.update(this.newMapa)
      .subscribe(res => {
        this.loading = false;
        this.callToast('success', 'Atualizado com sucesso!', 'Mapa atualizado');
      },
        error => {
          this.loading = false;
        });

  }

  novoMapa() {
    this.display = false;
    this.novo = true;
  }

  listar() {
    this.display = true;
    this.novo = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    const p = this.mapaService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Atualizado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  close() {
    this.modalCtrl.dismiss(this.requestUpdate, null, 'princi');
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

}
